import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { BranchFreightsReportComponent } from "src/app/back-end/reports/branch-freights-report/branch.freights.report.component";
import { ClinetFerightDetailsComponent } from "src/app/back-end/reports/clinet-freight-details/clinet-freight-details.component";
import { ClinetFreightsReportComponent } from "src/app/back-end/reports/clinet-freights-report/clinet.freights.report.component";
import { FerightDetailsComponent } from "src/app/back-end/reports/freight-details/freight-details.component";
import { AdminAuthCanActive } from "src/services/authentcation/admin.auth.can.active.service";
import { AdminOrClinetAuthCanActive } from "src/services/authentcation/admin.or.clinet.auth.can.active.service";
import { ClinetAuthCanActive } from "src/services/authentcation/clinet.auth.can.active.service";

const ROUTS: Routes = [
    { path: 'freights', component: ClinetFreightsReportComponent, canActivate: [ClinetAuthCanActive] },
    { path: 'freights/:userTargetId', component: ClinetFreightsReportComponent, canActivate: [AdminOrClinetAuthCanActive] },
    { path: 'freights/details/:targetId', component: ClinetFerightDetailsComponent, canActivate: [AdminOrClinetAuthCanActive] },
    { path: 'freights/fullDetails/:targetId', component: FerightDetailsComponent, canActivate: [AdminAuthCanActive] },
    
    { path: 'branch/freights/details/:targetId/:branchTargetId', component: ClinetFerightDetailsComponent, canActivate: [AdminAuthCanActive] },
    { path: 'branch/freights/:branchTargetId', component: BranchFreightsReportComponent, canActivate: [AdminAuthCanActive] },
];

@NgModule({
    imports: [RouterModule.forChild(ROUTS)],
    exports: [RouterModule]
})
export class ReportsRoutingModule {

}

