import { ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { SharedComponents } from 'src/app/shared.components';
import { ITransaction } from 'src/interfaces/i.transaction.interface';
import { FOPModel } from 'src/models/fop/fop.model';
import { AlertService } from 'src/services/alert.service';
import { ReportService } from 'src/services/report.service';

@Component({
  selector: 'app-freight',
  templateUrl: './clinet.freights.report.component.html',
  styles: [
  ]
})
export class ClinetFreightsReportComponent extends SharedComponents implements OnInit {
  skip: number = 0;
  limit: number = 50;
  entries: ITransaction[] = [];
  fop: FOPModel = new FOPModel();
  fopSearch: string;

  //         
  isAllowShow_landFreightPrice = false;
  isAllowShow_maritimeFreightPrice = false;
  isAllowShow_auctionFines = false;
  isAllowShow_storeFines = false;
  isAllowShow_title = false;
  isAllowShow_swift = false;
  isAllowShow_sellPrice = false;
  isAllowShow_commissionPurchase = false;

  clinetTargetId: string;
  clinetTagetName: string;
  maritimeFreightPriceTotalDue: number;
  //Is Current User Not Clinet
  isNotClinet:boolean;
  constructor(public service: ReportService, public activatedRoute: ActivatedRoute) { super() }

  ngOnInit(): void {
    this.isNotClinet=this.activatedRoute.snapshot.params.userTargetId?true:false;
    this.clinetTargetId = this.activatedRoute.snapshot.params.userTargetId||this.service.u.u._id;
    this.clinetTagetName = this.activatedRoute.snapshot.queryParams.userName;
    this.service.initClinetFreightsFilterForm(this.clinetTargetId);
    this.service.seo.updateSEO("freightsReport");
    this.service.getItems();
    this.getEntiries(true);
    this.getMaritimeFreightPriceTotalDue();
  }


  /**
 * Get My Users
 */
  getEntiries(isReset): void {
    if (isReset) {
      this.skip = 0;
      this.entries = [];
      this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
    }
    this.fop.paging.isRequestingToServer = true;

    //Get Users Now From Server
    this.allSubscriptions.push(
      this.service.http.getClinetFreightsReport<ITransaction[]>(this.skip, this.limit, this.service.form.value).subscribe(res => {
        //Update FOP Properties
        if (res.noMoreOfResult)
          this.fop.paging.disableGetMoreButton = true;
        this.fop.paging.isRequestingToServer = false;

        if (res.isSuccess) {
          this.entries = [...this.entries, ...res.result];



          this.isAllowShow_landFreightPrice = this.entries.findIndex(e => +e.landFreightPrice > 0) > -1;
          this.isAllowShow_maritimeFreightPrice = this.entries.findIndex(e => +e.maritimeFreightPrice > 0) > -1;
          this.isAllowShow_auctionFines = this.entries.findIndex(e => +e.auctionFines > 0) > -1;
          this.isAllowShow_storeFines = this.entries.findIndex(e => +e.storeFines > 0) > -1;
          this.isAllowShow_title = this.entries.findIndex(e => +e.titlePrice > 0) > -1;
          this.isAllowShow_swift = this.entries.findIndex(e => +e.swift > 0) > -1;
          this.isAllowShow_sellPrice = this.entries.findIndex(e => +e.sellPrice > 0) > -1;
          this.isAllowShow_commissionPurchase = this.entries.findIndex(e => +e.commissionPurchase > 0) > -1;


          //Fop 
          this.fop = new FOPModel(this.fop.fieldNames, this.entries.length);
          this.skip += this.limit;
        } else {
          AlertService.showResponseMessage(res);
        }
      }, error => {
        AlertService.canRequestError(error);
        this.fop.paging.isRequestingToServer = false;
      }, () => {
        this.fop.paging.isRequestingToServer = false;
      }));
  }

  /**
   *       
   */
  getMaritimeFreightPriceTotalDue() {
 this.allSubscriptions.push(
  this.service.http.getMaritimeFreightPriceTotalDueFromClinet<number>(this.clinetTargetId).subscribe(res => {

    if (res.isSuccess) {
this.maritimeFreightPriceTotalDue=res.result
    } else {
      AlertService.showResponseMessage(res);
    }
  }, error => {
    AlertService.canRequestError(error);
    this.fop.paging.isRequestingToServer = false;
  }, () => {
    this.fop.paging.isRequestingToServer = false;
  }));
  }

  funTrack(index: number, entitiy: ITransaction): string | undefined {
    return entitiy?._id;
  }
}//End CLass
